import ToDoItem from "./class/ToDoItem.js";
import Stack from "./class/Stack.js";

let id = 0;
const redoStack = [];
const undoStack = [];
const stackStatus = ['complete', 'incomplete'];

const init = () => {
  let addBtn = document.querySelector(".add-btn");
  addBtn.addEventListener('click', addItem);
  document.addEventListener('keyup', (event)=>{if(event.ctrlKey && event.keyCode === 90) undo()});
  document.addEventListener('keyup', (event)=>{if(event.ctrlKey && event.keyCode === 89) redo()});
}

const addItem = () => {
    let inputValue = getInput();
    let itemObj = {
        value: inputValue,
        id: id,
        status: "incomplete",
    };
    let inputAuthor = getInput();
    let authorObj = {
        value : inputAuthor,
        id : id,
        status: "incomplete",
    };
    let inputDate = getInput();
    let DateObj = {
        value : inputDate,
        id : id,
        status: "incomplete",
    };
  if (itemObj.value){
    let toDoDiv = document.querySelector("#incomplete-div");
    toDoDiv.appendChild(createItem("incomplete", itemObj));
    addUndoStack("add", itemObj);
    id++;
    clearInput();
  } else {
    alert("Todo Item cannot be empty");
  }
}


const getInput = () => {
  let inputValue = document.getElementById("input-box").value;
  let inputDate = document.getElementById("date-box").value;
  let inputAuthor = document.getElementById("author-box").value;
  let todoitem = "Author:" + inputAuthor+"  TodoItem:"+inputValue+"      Date:"+inputDate;
  return todoitem;

}

const clearInput = () => {
  let inputBox = document.querySelector("#input-box");
  let inputAuthor = document.querySelector("#author-box");
  let inputDate = document.querySelector("#date-box");
  inputBox.value = "";
  inputAuthor.value = "";
  inputDate.value = "";
};

const completeItem = (event) => {
  let itemObj = removeItem(event);
  let completeDiv = document.querySelector("#complete-div")
  completeDiv.appendChild(createItem("complete", itemObj));
  addUndoStack("complete", itemObj);
}

const incompleteItem = (event) => {
  let itemObj = removeItem(event);
  let completeDiv = document.querySelector("#incomplete-div")
  completeDiv.appendChild(createItem("incomplete", itemObj,));
  addUndoStack("incomplete", itemObj);
}

const deleteItem = (event) => {
  let itemObj = removeItem(event);
  let authorObj = removeItem(event);
  addUndoStack("delete", itemObj,authorObj);
}

const createItem = (status, itemObj) => {
  let toDoItem = new ToDoItem(status, itemObj.id, itemObj.value, completeItem, incompleteItem, deleteItem);
  let newItem = toDoItem.create();
  return newItem;
}

const removeItem = (event) => {
  let itemObj = {
    value : event.target.parentNode.childNodes[0].innerText,
    id : event.target.parentNode.id,
    status: event.target.parentNode.className,
  };
  event.target.parentNode.remove();
  return itemObj;
}

const addUndoStack = (action, itemObj)=>{
  let stack = new Stack(action, itemObj.id, itemObj.value, itemObj.status);
  undoStack.push(stack);
}

const addRedoStack = (action, itemObj)=>{
  let stack = new Stack(action, itemObj.id, itemObj.value, itemObj.status);
  redoStack.push(stack);
}

const undo = () =>{
  if(undoStack.length){
    const stack = undoStack.pop();
    const newStack = statusSwitch(stack);
    switch(stack.action){
      case "add":
        addRedoStack("delete", newStack);
        searchParent("incomplete-div", stack).remove();
        break;
      case "complete":
        addRedoStack("incomplete", newStack);
        searchParent("complete-div", stack).remove();
        addBackItem(stack);
        break;
      case "incomplete":
        addRedoStack("complete", newStack);
        searchParent("incomplete-div", stack).remove();
        addBackItem(stack);
        break;
      case "delete":
        addRedoStack("add", newStack);
        addBackItem(stack);
        break;
      default:
        alert('no matched action');
    }
  } else {
    alert("no undo stack");
  }
}

const redo = () =>{
  if(redoStack.length){
    let stack = redoStack.pop();
    const newStack = statusSwitch(stack);
    switch(stack.action){
      case "add":
        addUndoStack("delete", newStack);
        searchParent(`${newStack.status}-div`, newStack).remove();
        break;  
      case "complete":
        addUndoStack("incomplete", newStack);
        searchParent("complete-div", stack).remove();
        addBackItem(stack);
        break;
      case "incomplete":
        addUndoStack("complete", newStack);
        searchParent("incomplete-div", stack).remove();
        addBackItem(stack);
        break;
      case "delete":
        addUndoStack("add", newStack);
        addBackItem(newStack);
        break;
      default:
        alert('no matched action');
    }
  } else {
    alert("no redo stack");
  }
}

const searchParent = (section, stack)=>{
  let targetGrand = document.getElementById(section);
  let parentNodesArr = targetGrand.childNodes;
  for (let parent of parentNodesArr) {
    // why i need to parse them to int???
    if(parseInt(parent.id) === parseInt(stack.id)) {
       return parent;
    } 
  }
}

const statusSwitch = (stack)=>{
  const stackTemp = Object.create(stack);
  if(stackTemp.status === stackStatus[0]){
    stackTemp.status = stackStatus[1];
  } else {
    stackTemp.status = stackStatus[0];
  }
  return stackTemp;
}

const addBackItem = (stack)=>{
  let section = document.getElementById(`${stack.status}-div`)
  section.appendChild(createItem(stack.status, stack));
}

init();