export default class Stack {
  constructor(action, id, value, status) {
    this.action = action;
    this.id = id;
    this.value = value;
    this.status = status;
  }

  getAction(){
    return this.action; 
  }

  getID(){
    return this.id;
  }

  getValue(){
    return this.value;
  }

  getStatus(){
    return this.status;
  }
}